import React, { useState } from 'react';
import { motion } from 'framer-motion';
import Link from 'next/link';
import Image from 'next/image';

// Animation variants améliorés
const ANIMATIONS = {
  card: {
    hidden: { opacity: 0, y: 50, scale: 0.95 },
    visible: (delay) => ({
      opacity: 1,
      y: 0,
      scale: 1,
      transition: {
        duration: 0.7,
        ease: [0.22, 1, 0.36, 1],
        delay,
        staggerChildren: 0.1
      }
    }),
    hover: {
      y: -15,
      transition: {
        duration: 0.4,
        ease: 'easeOut'
      }
    }
  },
  image: {
    hidden: { opacity: 0, scale: 1.1 },
    visible: {
      opacity: 1,
      scale: 1,
      transition: { duration: 0.5, ease: 'easeOut' }
    },
    hover: {
      scale: 1.08,
      transition: {
        duration: 0.7,
        ease: [0.22, 1, 0.36, 1]
      }
    }
  },
  title: {
    hidden: { opacity: 0, y: 20 },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { duration: 0.5, ease: 'easeOut' }
    }
  },
  description: {
    hidden: { opacity: 0, y: 15 },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { duration: 0.5, ease: 'easeOut', delay: 0.1 }
    }
  },
  tags: {
    hidden: { opacity: 0 },
    visible: { 
      opacity: 1,
      transition: { duration: 0.5, ease: 'easeOut', delay: 0.2 }
    }
  },
  link: {
    hidden: { opacity: 0, x: -10 },
    visible: { 
      opacity: 1, 
      x: 0,
      transition: { duration: 0.5, ease: 'easeOut', delay: 0.3 }
    }
  }
};

// Composant pour les tags de technologies
const TechTag = ({ name }) => (
  <motion.span 
    className="inline-block bg-accent/10 px-3 py-1 rounded-full text-sm text-accent border border-accent/20"
    whileHover={{ 
      y: -3, 
      backgroundColor: 'rgba(56, 189, 248, 0.2)',
      transition: { duration: 0.2 }
    }}
  >
    {name}
  </motion.span>
);

// Composant pour le lien "Voir le projet"
const ProjectLink = ({ href }) => (
  <Link href={href} passHref>
    <motion.span
      className="inline-flex items-center text-accent font-medium group"
      whileHover={{ x: 5 }}
      transition={{ duration: 0.2 }}
    >
      Voir le projet
      <svg
        className="ml-2 w-4 h-4 group-hover:translate-x-1 transition-transform"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M14 5l7 7m0 0l-7 7m7-7H3"
        />
      </svg>
    </motion.span>
  </Link>
);

export default function ProjectCard({ project, delay = 0 }) {
  const [isHovered, setIsHovered] = useState(false);
  const { title, description, image, technologies, link, github } = project;

  return (
    <motion.div
      className="project-card h-full flex flex-col bg-white/80 backdrop-blur-sm rounded-xl overflow-hidden shadow-lg border border-accent-light/20 relative mouse-effect"
      variants={ANIMATIONS.card}
      custom={delay}
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, margin: '-100px' }}
      whileHover="hover"
      onHoverStart={() => setIsHovered(true)}
      onHoverEnd={() => setIsHovered(false)}
    >
      <div className="absolute inset-0 bg-gradient-to-b from-accent/5 to-transparent opacity-0 hover:opacity-100 transition-opacity duration-500 pointer-events-none z-0"></div>
      
      <Link href={link} passHref className="block overflow-hidden relative">
        <div className="relative w-full aspect-[16/9] overflow-hidden">
          <motion.div 
            className="h-full w-full" 
            variants={ANIMATIONS.image}
          >
            <Image
              src={image || '/assets/projects/placeholder.jpg'}
              alt={title}
              className="object-cover transform-gpu"
              fill
              sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 33vw"
            />
            <div className="absolute inset-0 bg-gradient-to-t from-black/50 to-transparent opacity-0 group-hover:opacity-100 transition-opacity"></div>
          </motion.div>
          
          {/* GitHub icon */}
          {github && (
            <motion.a 
              href={github}
              target="_blank"
              rel="noopener noreferrer"
              className="absolute top-3 right-3 bg-black/60 hover:bg-black text-white p-2 rounded-full backdrop-blur-sm z-10 transform-gpu"
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ 
                opacity: isHovered ? 1 : 0, 
                scale: isHovered ? 1 : 0.8,
                transition: { duration: 0.2 }
              }}
              whileHover={{ scale: 1.2 }}
            >
              <svg className="w-4 h-4" viewBox="0 0 24 24" fill="currentColor">
                <path d="M12 0C5.37 0 0 5.37 0 12c0 5.31 3.435 9.795 8.205 11.385.6.105.825-.255.825-.57 0-.285-.015-1.23-.015-2.235-3.015.555-3.795-.735-4.035-1.41-.135-.345-.72-1.41-1.23-1.695-.42-.225-1.02-.78-.015-.795.945-.015 1.62.87 1.845 1.23 1.08 1.815 2.805 1.305 3.495.99.105-.78.42-1.305.765-1.605-2.67-.3-5.46-1.335-5.46-5.925 0-1.305.465-2.385 1.23-3.225-.12-.3-.54-1.53.12-3.18 0 0 1.005-.315 3.3 1.23.96-.27 1.98-.405 3-.405s2.04.135 3 .405c2.295-1.56 3.3-1.23 3.3-1.23.66 1.65.24 2.88.12 3.18.765.84 1.23 1.905 1.23 3.225 0 4.605-2.805 5.625-5.475 5.925.435.375.81 1.095.81 2.22 0 1.605-.015 2.895-.015 3.3 0 .315.225.69.825.57A12.02 12.02 0 0024 12c0-6.63-5.37-12-12-12z" />
              </svg>
            </motion.a>
          )}
        </div>
      </Link>

      <div className="flex flex-col flex-grow p-6 mouse-effect-children relative z-10">
        <motion.h3 
          className="text-xl font-space font-medium mb-2 child-effect shine-text"
          variants={ANIMATIONS.title}
        >
          {title}
        </motion.h3>
        
        <motion.p 
          className="text-gray-medium mb-4 flex-grow child-effect"
          variants={ANIMATIONS.description}
        >
          {description}
        </motion.p>
        
        <motion.div className="mt-auto child-effect" variants={ANIMATIONS.tags}>
          <div className="flex flex-wrap gap-2 mb-4">
            {technologies?.map((tech, index) => (
              <TechTag key={index} name={tech} />
            ))}
          </div>
          
          <motion.div variants={ANIMATIONS.link}>
            <ProjectLink href={link} />
          </motion.div>
        </motion.div>
      </div>
      
      <div className="absolute -bottom-px left-0 w-full h-1 bg-gradient-to-r from-accent-light via-accent to-accent-light opacity-0 group-hover:opacity-100 transition-opacity"></div>
    </motion.div>
  );
} 