'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import ProjectCard from '@/components/ProjectCard';

export default function Projects() {
  const [activeFilter, setActiveFilter] = useState('Tous');
  const [animationCount, setAnimationCount] = useState(0);
  
  // Réinitialiser le compteur d'animation lors du changement de filtre
  useEffect(() => {
    setAnimationCount(prev => prev + 1);
  }, [activeFilter]);
  
  const projects = [
    {
      title: "E-commerce React",
      description: "Site e-commerce complet avec panier, paiement et gestion des commandes. Interface utilisateur intuitive et responsive.",
      image: "/assets/projects/ecommerce.jpg",
      category: "Site Web",
      technologies: ["React", "Node.js", "Express", "MongoDB"],
      link: "https://exemple-ecommerce.com",
      github: "https://github.com/username/ecommerce"
    },
    {
      title: "App de Tâches",
      description: "Application de gestion de tâches avec fonctionnalités de drag & drop, filtres et statistiques de productivité.",
      image: "/assets/projects/todo.jpg",
      category: "Application",
      technologies: ["React", "Redux", "Firebase"],
      link: "https://exemple-todo.com",
      github: "https://github.com/username/todo-app"
    },
    {
      title: "Dashboard Analytics",
      description: "Dashboard interactif visualisant des données complexes avec graphiques personnalisables et filtres avancés.",
      image: "/assets/projects/dashboard.jpg",
      category: "Dashboard",
      technologies: ["Vue.js", "D3.js", "Node.js"],
      link: "https://exemple-dashboard.com",
      github: "https://github.com/username/dashboard"
    },
    {
      title: "Landing Page SaaS",
      description: "Page d'atterrissage pour un service SaaS optimisée pour la conversion avec animations fluides.",
      image: "/assets/projects/landing.jpg",
      category: "Site Web",
      technologies: ["HTML5", "CSS3", "JavaScript", "GSAP"],
      link: "https://exemple-landing.com",
      github: "https://github.com/username/landing-page"
    },
    {
      title: "Application Météo",
      description: "Application météo avec prévisions détaillées, alertes et visualisation de données en temps réel.",
      image: "/assets/projects/weather.jpg",
      category: "Application",
      technologies: ["React Native", "API REST", "Chart.js"],
      link: "https://exemple-meteo.com",
      github: "https://github.com/username/weather-app"
    },
    {
      title: "Portfolio Responsive",
      description: "Site web portfolio personnel présentant mes projets et compétences avec une interface élégante et responsive.",
      image: "/assets/projects/portfolio.jpg",
      category: "Site Web",
      technologies: ["Next.js", "TailwindCSS", "Framer Motion"],
      link: "#",
      github: "https://github.com/username/portfolio"
    }
  ];
  
  // Obtenir toutes les catégories uniques
  const categories = ['Tous', ...new Set(projects.map(p => p.category))];
  
  // Filtrer les projets
  const filteredProjects = activeFilter === 'Tous' 
    ? projects 
    : projects.filter(p => p.category === activeFilter);

  // Composant pour la navigation des filtres
  const FilterNav = () => (
    <div className="mb-8 flex justify-center flex-wrap gap-2">
      {categories.map((category) => (
        <button
          key={category}
          onClick={() => setActiveFilter(category)}
          className={`px-4 py-2 rounded-full transition-all duration-300 ${
            activeFilter === category
              ? 'bg-accent text-white shadow-md'
              : 'bg-white/80 text-gray-dark hover:bg-accent/10'
          }`}
        >
          {category}
        </button>
      ))}
    </div>
  );

  return (
    <section id="projects" className="py-20 particles-bg">
      <div className="container-wide">
        <div className="mb-12 max-w-xl mx-auto text-center">
          <span className="badge mb-3 inline-block">Portfolio</span>
          <h2 className="mb-5 shine-text">Projets sélectionnés</h2>
          <p className="mb-8">Une collection de projets récents qui illustrent mon expertise en développement web et en conception d'interfaces.</p>
          
          <FilterNav />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 md:gap-8">
          {filteredProjects.map((project, index) => (
            <div 
              key={`${project.title}-${animationCount}`} 
              className="fade-in-up" 
              style={{ animationDelay: `${index * 0.15}s` }}
            >
              <ProjectCard project={project} delay={index * 0.1} />
            </div>
          ))}
        </div>
        
        {filteredProjects.length === 0 && (
          <div className="text-center py-12">
            <p className="text-xl text-gray-dark">Aucun projet dans cette catégorie pour le moment.</p>
          </div>
        )}
        
        <div className="text-center mt-16">
          <a 
            href="https://github.com/votre-username" 
            target="_blank" 
            rel="noopener noreferrer" 
            className="inline-flex items-center gap-2 btn btn-outline mouse-effect glow-border"
          >
            <svg className="w-5 h-5" viewBox="0 0 24 24" fill="currentColor">
              <path d="M12 0C5.37 0 0 5.37 0 12c0 5.31 3.435 9.795 8.205 11.385.6.105.825-.255.825-.57 0-.285-.015-1.23-.015-2.235-3.015.555-3.795-.735-4.035-1.41-.135-.345-.72-1.41-1.23-1.695-.42-.225-1.02-.78-.015-.795.945-.015 1.62.87 1.845 1.23 1.08 1.815 2.805 1.305 3.495.99.105-.78.42-1.305.765-1.605-2.67-.3-5.46-1.335-5.46-5.925 0-1.305.465-2.385 1.23-3.225-.12-.3-.54-1.53.12-3.18 0 0 1.005-.315 3.3 1.23.96-.27 1.98-.405 3-.405s2.04.135 3 .405c2.295-1.56 3.3-1.23 3.3-1.23.66 1.65.24 2.88.12 3.18.765.84 1.23 1.905 1.23 3.225 0 4.605-2.805 5.625-5.475 5.925.435.375.81 1.095.81 2.22 0 1.605-.015 2.895-.015 3.3 0 .315.225.69.825.57A12.02 12.02 0 0024 12c0-6.63-5.37-12-12-12z"/>
            </svg>
            Voir plus de projets sur GitHub
          </a>
        </div>
      </div>
    </section>
  );
} 